
/* -------------------------------------- */
/*                 NAVBAR                 */
/* -------------------------------------- */

.container-navbar {
    background-color: var(--primary-color);
}

.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem, 0;
}

.navbar .fa-bars {
    display: none;
}

.navbar img {
    width: 6rem;
}

.navbar .menu {
    display: flex;
    gap: 2rem;
    padding: 1.7rem;
}

.navbar .menu li {
    list-style: none;
}

.navbar .menu .navlink {
    text-decoration: none;
    color: var(--secundary-color);
    font-size: 1.6rem;
    font-weight: 600;
    text-transform: uppercase;
    position: relative;
    transition: all .4s ease;
}
.navbar .menu .navlink::after {
    content: '';
    width: 1.5rem;
    height: 2px;
    background-color: #fff;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translate( -50%, 50% );
    opacity: 0;
    transition: all .4s ease;
}

.navbar .menu .navlink:hover {
    color: #fff;
}

.navbar .menu .navlink:hover::after {
    opacity: 1;
}

.navbar .menu .nav-active {
    color: #fff;
}

.navbar .menu .nav-active::after {
    content: '';
    width: 1.5rem;
    height: 2px;
    background-color: #fff;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translate( -50%, 50% );
    opacity: 1;
}