/* -------------------------------------- */
/*          CONFIGURACION GENERAL         */
/* -------------------------------------- */

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap');

:root {
    --primary-color: #001c41;
    --secundary-color: rgb(0, 231, 231);
    --background-color: #f9f5f0;
    --dark-color: #151515;
}

html {
    font-size: 62.5%;
    font-family: 'Quicksand', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}




/* -------------------------------------- */
/*               UTILIDADES               */
/* -------------------------------------- */

.container {
    max-width: 120rem;
    margin: 0 auto;
}

.heading-1 {
    text-align: center;
    font-weight: 400;
    font-size: 4rem;
    position: relative;
}

.heading-1 span {
    font-weight: bold;
}

.heading-1::after {
    content: '';
    width: 6.5rem;
    height: 3px;
    background-color: #1359b6;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translate( -50%, 50% );
}


/* ---------------------------------------------------------------------------------------------------------------------------- */
/* ---------------------------------------------------------------------------------------------------------------------------- */


/* -------------------------------------- */
/*                 BANNER                 */
/* -------------------------------------- */

.container-banner {
    background-image: linear-gradient( 100deg, #000000, #00000020 ), url( '../img/banner3.jpg' );
    height: 55rem;
    background-size: cover;
    background-position: center;
}

.content-banner {
    max-width: 90rem;
    margin: 0 auto;
    padding: 10rem 0;
}

.content-banner h1 {
    font-size: 5.5rem;
    color: #fff;
    font-weight: 700;
}

.container-banner p {
    color: #ffffffc0;
    font-size: 1.8rem;
    margin-top: 2rem;
    font-weight: 500;
}

.container-banner span {
    margin-top: 2rem;
    text-decoration: none;
    color: #fff;
    background-color: #1359b6;
    display: inline-block;
    padding: 1rem 3rem;
    font-weight: 600;
    text-transform: uppercase;
    border-radius: 3rem;
    cursor: pointer;
    transition: all .5s ease;
}

.container-banner span:hover {
    background-color: #fff;
    color: var(--dark-color);
}




/* -------------------------------------- */
/*                COUNTER                 */
/* -------------------------------------- */

.contadores-principal {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    margin-bottom: 6rem;
}

.contador {
    width: 27.7%;
    text-align: center;
    display: inline-block;
}


.contador_cantidad {
    color: #114a96;
    font-size: 5.5rem;
    margin: 1rem 0;
    font-weight: 1000;
}

.contador h2 {
    font-size: 2.5rem;
}

.contador .fa-facebook-square {
    color: rgb( 66, 103, 178 );
}

.contador .fa-youtube {
    color: red;
}

.ocultar {
    opacity: 0;
}

.animar {
    animation: aparecer 1.3s;
}

@keyframes aparecer {
    0% {
        opacity: 0;
        transform: translateY( 10rem );
    }

    100% {
        opacity: 1;
        transform: translateY( 0 );
    }
}




/* -------------------------------------- */
/*             QUIENES SOMOS              */
/* -------------------------------------- */

.quienes-somos {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-bottom: 5rem;
    margin-top: 4rem;
}

.quienes-somos ul {
    display: flex;
    gap: 5rem;
    justify-content: center;
    align-items: center;
}

.quienes-somos ul li {
    list-style: none;
    margin: 0rem 2rem;
    margin-top: 2rem;
}

.quienes-somos ul li img {
    width: 10rem;
    border-radius: 100%;
    border: 5px solid #1359b6;

}

.quienes-somos-text {
    display: flex;
    margin: 1rem 15rem;
}

.quienes-somos-text img {
    width: 40%;
    height:auto;
    position: cover;
    background-size: cover;
    margin-right: 2rem;
    border-radius: 3%;
}

.quienes-somos-text p {
    font-size: 2rem;
    font-weight: 300;
}




/* -------------------------------------- */
/*           NUESTROS SERVICIOS           */
/* -------------------------------------- */

.main-nuestros-servicios {
    background-color: #000042;
}

.nuestros-servicios {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding-bottom: 4rem;
    padding-top: 3rem;
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.nuestros-servicios h1 {
    color: #f9f5f0;
}

.container-gallery {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: repeat( 4, 1fr);
    grid-template-rows: repeat(2, 32rem);
    gap: 2.5rem;
}

.card-servicio {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.gallery-img-1 {
    background-image: linear-gradient( #00000080, #00000080 ), url('../img/theme-service1.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.gallery-img-2 {
    background-image: linear-gradient( #00000090, #00000090 ), url('../img/theme-service2.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.gallery-img-3 {
    background-image: linear-gradient( #00000090, #00000090 ), url('../img/theme-service3.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.gallery-img-4 {
    background-image: linear-gradient( #00000090, #00000090 ), url('../img/theme-service1.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.gallery-img-5 {
    grid-row: 1/2;
    grid-column: 2/4;
    background-image: linear-gradient( #00000090, #00000090 ), url('../img/theme-service5.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.gallery-img-6 {
    grid-row: 2/2;
    grid-column: 2/4;
    background-image: linear-gradient( #00000090, #00000090 ), url('../img/theme-service4.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.card-servicio .textServicio {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 1.5rem;
    color: #fff;
    font-weight: 600;
    margin-top: 1.5rem;
    padding: 0rem 3rem;
    position: relative;
}

.card-servicio .ver-mas {
    display: flex;
    margin: 2rem;
    font-weight: 800;
    font-size: 1.2rem;
    padding: 1rem 3rem;
    background-color: #1359b6;
    color: #f9f5f0;
    border-radius: 2rem;
    cursor: pointer;
    transition: ease .4s all;
}

.card-servicio .ver-mas:hover {
    background-color: #f9f5f0;
    color: var(--dark-color);
}

.card-servicio .card-title {
    color: #f9f5f0;
    font-size: 3rem;
    font-weight: 800;
    position: relative;
}

.card-servicio .card-title::after {
    content: '';
    width: 15rem;
    height: 2px;
    background-color: rgb(199, 199, 199);
    position: absolute;
    bottom: -.8rem;
    left: 50%;
    transform: translate( -50%, 50% );

}

.card-servicio p::after {
    content: '';
    width: 10rem;
    height: 2px;
    background-color: rgb(199, 199, 199);
    position: absolute;
    bottom: -.5rem;
    left: 50%;
    transform: translate( -50%, 50% );

}

.gallery-img-6 .card-title::after,
.gallery-img-5 .card-title::after {
    width: 45rem;
}

.gallery-img-6 p::after,
.gallery-img-5 p::after {
    width: 30rem;
    bottom: -.5rem;
}




/* -------------------------------------- */
/*         PROYECTOS REALIZADOS           */
/* -------------------------------------- */

.content-proyectos-realizados {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
}

.linear {
    margin-bottom: 4rem;
} 

.content-proyectos-realizados .card {
    width: 70%;
    margin: 2rem;
    border-radius: 6px;
    overflow: hidden;
    background-color: #f9f5f0;
    box-shadow: 0px 1px 10px rgb( 0, 0, 0, 0.2 );
    cursor: default;
    transition: all 400ms ease;
}

.content-proyectos-realizados .card:hover {
    box-shadow: 5px 5px 20px rgb( 0, 0, 0, 0.4 );
    transform: translateY( -3% );
}

.content-proyectos-realizados .card img {
    width: 100%;
    height: 250px;
}

.content-proyectos-realizados .card .contenido {
    padding: 1.5rem;
    text-align: center;
}

.content-proyectos-realizados .card .contenido p {
    font-size: 1.5rem;
    line-height: 1.5;
    color: #6a6a6a;
    margin-bottom: 1rem;
}

.content-proyectos-realizados .card .contenido h3 {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.content-proyectos-realizados .card .contenido a {
    text-decoration: none;
    display: inline-block;
    padding: 1rem 2rem;
    margin-top: 1rem;
    background-color: #1359b6;
    font-size: 1.3rem;
    font-weight: 600;
    border-radius: 20px;
    color: #fff;
    transition: all .4s ease;
}

.content-proyectos-realizados .card .contenido a:hover {
    color: #1359b6;
    background-color: #fff;
}




/* -------------------------------------- */
/*               CONFIARON                */
/* -------------------------------------- */

.container-confiaron ul .swiper {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin: 6rem 12rem;
    padding-left: 5rem;
    padding-right: 5rem;
}

.container-confiaron ul li {
    padding-bottom: 6rem;
    cursor: pointer;
}

.container-confiaron ul li img {
    width: 10rem;
    height: 10rem;
}



.formulario-contacto {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
}

.content-formulario {
    margin: 4.5rem 0;
    gap: 2.5rem;
    justify-content: center;
    align-items: center;
    display: grid;
    grid-template-columns: repeat( 3, 1fr);
}

.formulario {
    grid-column: 1/3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #F7F7F7;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 0px 1px 10px rgb( 0, 0, 0, 0.2 );

}

.formulario h3 {
    margin-bottom: 2rem;
    font-size: 2rem;
    font-weight: 600;
    color: #000069;
}

.formulario input {
    outline: none;
    margin: 0.5rem;
    font-size: 1.8rem;
    padding: 1.2rem;
    border-radius: 0.5rem;
    border: 1px solid #C2C2C2;
    color: #6a6a6a;
    font-weight: 450;
    font-family: inherit;
}

.input-asunto input {
    width: 49.5rem;
}

.formulario textarea {
    outline: none;
    margin: 1.5rem 0.5rem;
    font-size: 1.8rem;
    padding: 1.2rem;
    border-radius: 0.5rem;
    border: 1px solid #C2C2C2;
    color: #6a6a6a;
    font-weight: 450;
    font-family: inherit;
    width: 49.5rem;
    height: 15rem;
}

.formulario span {
    font-size: 1.6rem;
    font-weight: 500;
    background-color: #1359b6;
    padding: .8rem 2rem;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    transition: ease .4s all;
}

.formulario span:hover {
    background-color: #104386;
}



/* ---------------------------------------------------------------------------------------------------------------------------- */
/* ---------------------------------------------------------------------------------------------------------------------------- */


/* -------------------------------------- */
/*         MEDIA QUERIES --1068PX          */
/* -------------------------------------- */

@media ( max-width: 1068px ) {

    .socials {
        display: none;
    }

    .sitio-contruccuion {
        margin: 3rem, 3rem;
        height: 55rem;
    }

    .container-footer {
        gap: 3.5rem;
        padding: 3.5rem;
    }

    .menu-footer {
        grid-template-columns: repeat( 1, 1fr ) 30rem;
        gap: 2.5rem;
        align-items: center;
        text-align: center;
        justify-content: center;
    }

    .info, .menufooter, .informacion-adicional, .contacto {
        gap: 1rem;
    }

    .info img {
        width: 18rem;
    }

    .contacto .redes-sociales {
        justify-content: center;
        text-align: center;
        align-items: center; 
    }
    
}




/* -------------------------------------- */
/*         MEDIA QUERIES --768PX          */
/* -------------------------------------- */

@media ( max-width: 768px ) {
    
    html {
        font-size: 55%;
    }

    .sitio-contruccuion {
        margin: 1.5rem, 1.5rem;
        height: 45rem;
    }
    
    .sitio-contruccuion img {
        width: 65%;
    }
    .container-header {
        height: 23rem;
    }

    .container-header img {
        width: 27rem;
    }

    .info img {
        width: 16rem;
    }

}




/* -------------------------------------- */
/*         MEDIA QUERIES --468PX          */
/* -------------------------------------- */

@media ( max-width: 468px ) {

    html {
        font-size: 42.5%;
    }

    .sitio-contruccuion {
        margin: 1rem, 1rem;
        height: 40rem;
    }
    
    .sitio-contruccuion img {
        width: 70%;
    }

    .container-header {
        height: 20rem;
    }

    .container-header img {
        width: 24rem;
    }


}